/* @import "../node_modules/normalize.css/normalize.css"; */

* {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  font-family: "Nunito", "Arial", "Helvetica", sans-serif;
  margin: 0;
}
h1 {
  font-size: 3rem;
  color: inherit;
}
a {
  text-decoration: none;
  color: inherit;
}
